import { Component, Prop, State, Event, EventEmitter, h, Watch, Element, forceUpdate } from '@stencil/core';
import {
  createTable,
  getCoreRowModel,
  getSortedRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getExpandedRowModel,
  getGroupedRowModel,
  type Table,
  type ColumnDef,
  type SortingState,
  type ColumnFiltersState,
  type RowSelectionState,
  type ExpandedState,
  type VisibilityState,
  type Row,
  type Header,
  type Cell,
  type TableState,
  type Updater,
} from '@tanstack/table-core';

import type {
  DataGridSize,
  DataGridSelectionMode,
  DataGridEditMode,
  DataGridLoadingType,
  DataGridCaptionPosition,
  DataGridSortChangeEvent,
  DataGridFilterChangeEvent,
  DataGridSelectionChangeEvent,
  DataGridPageChangeEvent,
  DataGridRowClickEvent,
  DataGridCellClickEvent,
  DataGridExpandChangeEvent,
  DataGridGroupToggleEvent,
  DataGridEditStartEvent,
  DataGridEditCancelEvent,
  DataGridEditSaveEvent,
  DataGridCellChangeEvent,
  DataGridValidationErrorEvent,
  RowData,
  GetRowIdFn,
  GetSubRowsFn,
  DataGridColumnMeta,
} from './bh-data-grid.types';

// Extend TanStack's ColumnMeta with our custom properties
declare module '@tanstack/table-core' {
  // eslint-disable-next-line @typescript-eslint/no-empty-object-type
  interface ColumnMeta<TData, TValue> extends DataGridColumnMeta {}
}

@Component({
  tag: 'bh-data-grid',
  styleUrl: 'bh-data-grid.css',
  shadow: true,
})
export class BhDataGrid<TData extends RowData = RowData> {
  @Element() el!: HTMLElement;

  // ===========================================================================
  // Props - Data
  // ===========================================================================

  /** Array of data objects to display */
  @Prop() data: TData[] = [];

  /** Column definitions (TanStack ColumnDef format) */
  @Prop() columns: ColumnDef<TData, unknown>[] = [];

  /** Function to get unique row ID */
  @Prop() getRowId?: GetRowIdFn<TData>;

  /** Function to get sub-rows for expansion */
  @Prop() getSubRows?: GetSubRowsFn<TData>;

  // ===========================================================================
  // Props - Display
  // ===========================================================================

  /** Size variant */
  @Prop() size: DataGridSize = 'default';

  /** Show striped rows */
  @Prop() striped: boolean = false;

  /** Show borders */
  @Prop() bordered: boolean = false;

  /** Sticky header */
  @Prop() stickyHeader: boolean = false;

  /** Max height for scrollable container (enables sticky header) */
  @Prop() maxHeight?: string;

  /** Caption text */
  @Prop() caption?: string;

  /** Caption position */
  @Prop() captionPosition: DataGridCaptionPosition = 'top';

  /** Empty state title */
  @Prop() emptyTitle: string = 'No data';

  /** Empty state description */
  @Prop() emptyDescription: string = 'There are no records to display.';

  // ===========================================================================
  // Props - Loading
  // ===========================================================================

  /** Loading state */
  @Prop() loading: boolean = false;

  /** Loading type */
  @Prop() loadingType: DataGridLoadingType = 'spinner';

  /** Number of skeleton rows to show */
  @Prop() skeletonRowCount: number = 5;

  // ===========================================================================
  // Props - Sorting
  // ===========================================================================

  /** Enable sorting */
  @Prop() enableSorting: boolean = true;

  /** Enable multi-column sorting */
  @Prop() enableMultiSort: boolean = false;

  /** Initial/controlled sort state */
  @Prop({ mutable: true }) sortState: SortingState = [];

  // ===========================================================================
  // Props - Filtering
  // ===========================================================================

  /** Enable column filtering */
  @Prop() enableFiltering: boolean = false;

  /** Enable global filter (search) */
  @Prop() enableGlobalFilter: boolean = false;

  /** Show filter row below header */
  @Prop() showFilterRow: boolean = false;

  /** Global filter placeholder text */
  @Prop() globalFilterPlaceholder: string = 'Search...';

  /** Initial/controlled filter state */
  @Prop({ mutable: true }) filterState: ColumnFiltersState = [];

  /** Initial/controlled global filter */
  @Prop({ mutable: true }) globalFilter: string = '';

  // ===========================================================================
  // Props - Selection
  // ===========================================================================

  /** Selection mode */
  @Prop() selectionMode: DataGridSelectionMode = 'none';

  /** Initial/controlled selected row IDs */
  @Prop({ mutable: true }) selectedRowIds: string[] = [];

  // ===========================================================================
  // Props - Pagination
  // ===========================================================================

  /** Enable pagination */
  @Prop() enablePagination: boolean = false;

  /** Current page (1-indexed) */
  @Prop({ mutable: true }) page: number = 1;

  /** Page size */
  @Prop({ mutable: true }) pageSize: number = 10;

  /** Page size options */
  @Prop() pageSizeOptions: number[] = [10, 25, 50, 100];

  // ===========================================================================
  // Props - Expansion
  // ===========================================================================

  /** Enable row expansion */
  @Prop() enableExpansion: boolean = false;

  /** Initial/controlled expanded row IDs */
  @Prop({ mutable: true }) expandedRowIds: string[] = [];

  // ===========================================================================
  // Props - Grouping
  // ===========================================================================

  /** Enable grouping */
  @Prop() enableGrouping: boolean = false;

  /** Column IDs to group by */
  @Prop() groupBy: string[] = [];

  // ===========================================================================
  // Props - Editing
  // ===========================================================================

  /** Enable editing */
  @Prop() enableEditing: boolean = false;

  /** Edit mode */
  @Prop() editMode: DataGridEditMode = 'cell';

  /** Show edit action buttons */
  @Prop() showEditActions: boolean = true;

  // ===========================================================================
  // Internal State
  // ===========================================================================

  @State() private tableState: Partial<TableState> = {};
  @State() private editingRowId: string | null = null;
  @State() private editingColumnId: string | null = null;
  @State() private editingValues: Record<string, unknown> = {};
  @State() private validationErrors: Record<string, string> = {};
  @State() private hoveredRowId: string | null = null;

  private table: Table<TData> | null = null;

  // ===========================================================================
  // Events
  // ===========================================================================

  @Event() bhSortChange!: EventEmitter<DataGridSortChangeEvent>;
  @Event() bhFilterChange!: EventEmitter<DataGridFilterChangeEvent>;
  @Event() bhSelectionChange!: EventEmitter<DataGridSelectionChangeEvent>;
  @Event() bhPageChange!: EventEmitter<DataGridPageChangeEvent>;
  @Event() bhRowClick!: EventEmitter<DataGridRowClickEvent>;
  @Event() bhCellClick!: EventEmitter<DataGridCellClickEvent>;
  @Event() bhExpandChange!: EventEmitter<DataGridExpandChangeEvent>;
  @Event() bhGroupToggle!: EventEmitter<DataGridGroupToggleEvent>;
  @Event() bhEditStart!: EventEmitter<DataGridEditStartEvent>;
  @Event() bhEditCancel!: EventEmitter<DataGridEditCancelEvent>;
  @Event() bhEditSave!: EventEmitter<DataGridEditSaveEvent>;
  @Event() bhCellChange!: EventEmitter<DataGridCellChangeEvent>;
  @Event() bhValidationError!: EventEmitter<DataGridValidationErrorEvent>;

  // ===========================================================================
  // Watchers
  // ===========================================================================

  @Watch('data')
  @Watch('columns')
  handleDataChange() {
    this.initializeTable();
  }

  @Watch('sortState')
  handleSortStateChange(newValue: SortingState) {
    if (this.table) {
      this.table.setSorting(newValue);
    }
  }

  @Watch('filterState')
  handleFilterStateChange(newValue: ColumnFiltersState) {
    if (this.table) {
      this.table.setColumnFilters(newValue);
    }
  }

  @Watch('selectedRowIds')
  handleSelectedRowIdsChange(newValue: string[]) {
    if (this.table) {
      const selection: RowSelectionState = {};
      newValue.forEach(id => { selection[id] = true; });
      this.table.setRowSelection(selection);
    }
  }

  @Watch('page')
  @Watch('pageSize')
  handlePaginationPropChange() {
    if (this.table) {
      this.table.setPagination({
        pageIndex: this.page - 1,
        pageSize: this.pageSize,
      });
    }
  }

  @Watch('expandedRowIds')
  handleExpandedRowIdsChange(newValue: string[]) {
    if (this.table) {
      const expanded: ExpandedState = {};
      newValue.forEach(id => { expanded[id] = true; });
      this.table.setExpanded(expanded);
    }
  }

  @Watch('groupBy')
  handleGroupByChange(newValue: string[]) {
    if (this.table) {
      this.table.setGrouping(newValue);
    }
  }

  // ===========================================================================
  // Lifecycle
  // ===========================================================================

  componentWillLoad() {
    this.initializeTable();
  }

  // ===========================================================================
  // Table Initialization
  // ===========================================================================

  private initializeTable() {
    // Build initial state
    const initialState: Partial<TableState> = {
      sorting: this.sortState,
      columnFilters: this.filterState,
      globalFilter: this.globalFilter,
      rowSelection: this.buildRowSelection(),
      expanded: this.buildExpanded(),
      pagination: {
        pageIndex: this.page - 1,
        pageSize: this.pageSize,
      },
      grouping: this.groupBy,
      columnVisibility: this.buildColumnVisibility(),
      columnPinning: { left: [], right: [] },
      columnSizing: {},
    };

    this.tableState = initialState;

    // Create table instance
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    this.table = createTable({
      data: this.data,
      columns: this.columns,
      state: this.tableState as TableState,
      onStateChange: (updater: Updater<TableState>) => {
        const newState = typeof updater === 'function'
          ? updater(this.tableState as TableState)
          : updater;
        this.handleStateChange(newState);
      },
      getCoreRowModel: getCoreRowModel(),
      getSortedRowModel: this.enableSorting ? getSortedRowModel() : undefined,
      getFilteredRowModel: this.enableFiltering || this.enableGlobalFilter ? getFilteredRowModel() : undefined,
      getPaginationRowModel: this.enablePagination ? getPaginationRowModel() : undefined,
      getExpandedRowModel: this.enableExpansion || this.enableGrouping ? getExpandedRowModel() : undefined,
      getGroupedRowModel: this.enableGrouping ? getGroupedRowModel() : undefined,
      getRowId: this.getRowId || ((row, index) => String((row as Record<string, unknown>).id ?? index)),
      getSubRows: this.getSubRows,
      enableRowSelection: this.selectionMode !== 'none',
      enableMultiRowSelection: this.selectionMode === 'multiple-click' || this.selectionMode === 'multiple-checkbox',
      enableSorting: this.enableSorting,
      enableFilters: this.enableFiltering || this.enableGlobalFilter,
      enableExpanding: this.enableExpansion || this.enableGrouping,
      enableGrouping: this.enableGrouping,
      manualPagination: false,
      autoResetExpanded: false,
      renderFallbackValue: null,
    } as any);

    // Force update after table creation
    forceUpdate(this.el);
  }

  private handleStateChange(newState: TableState) {
    const oldState = this.tableState;
    this.tableState = newState;

    // Update table options with new state
    if (this.table) {
      this.table.setOptions(prev => ({
        ...prev,
        state: newState,
      }));
    }

    // Emit events for state changes
    if (JSON.stringify(oldState.sorting) !== JSON.stringify(newState.sorting)) {
      this.sortState = newState.sorting;
      this.bhSortChange.emit({ sorting: newState.sorting });
    }

    if (JSON.stringify(oldState.columnFilters) !== JSON.stringify(newState.columnFilters) ||
        oldState.globalFilter !== newState.globalFilter) {
      this.filterState = newState.columnFilters;
      this.globalFilter = newState.globalFilter;
      this.bhFilterChange.emit({
        columnFilters: newState.columnFilters,
        globalFilter: newState.globalFilter,
      });
    }

    if (JSON.stringify(oldState.rowSelection) !== JSON.stringify(newState.rowSelection)) {
      const selectedIds = Object.keys(newState.rowSelection).filter(id => newState.rowSelection[id]);
      this.selectedRowIds = selectedIds;
      const selectedRows = this.table?.getSelectedRowModel().rows.map(r => r.original) ?? [];
      this.bhSelectionChange.emit({
        selectedRowIds: selectedIds,
        selectedRows,
      });
    }

    if (oldState.pagination?.pageIndex !== newState.pagination?.pageIndex ||
        oldState.pagination?.pageSize !== newState.pagination?.pageSize) {
      this.page = newState.pagination.pageIndex + 1;
      this.pageSize = newState.pagination.pageSize;
      this.bhPageChange.emit({
        page: this.page,
        pageSize: this.pageSize,
      });
    }

    // Force re-render
    forceUpdate(this.el);
  }

  private buildRowSelection(): RowSelectionState {
    const selection: RowSelectionState = {};
    this.selectedRowIds.forEach(id => { selection[id] = true; });
    return selection;
  }

  private buildExpanded(): ExpandedState {
    const expanded: ExpandedState = {};
    this.expandedRowIds.forEach(id => { expanded[id] = true; });
    return expanded;
  }

  private buildColumnVisibility(): VisibilityState {
    // Hide grouped columns
    const visibility: VisibilityState = {};
    if (this.enableGrouping) {
      this.groupBy.forEach(colId => { visibility[colId] = false; });
    }
    return visibility;
  }

  // ===========================================================================
  // Event Handlers
  // ===========================================================================

  private handleRowClick = (row: Row<TData>) => {
    const rowId = row.id;

    // Handle click-based selection
    if (this.selectionMode === 'single-click') {
      const isSelected = row.getIsSelected();
      this.table?.resetRowSelection();
      if (!isSelected) {
        row.toggleSelected(true);
      }
    } else if (this.selectionMode === 'multiple-click') {
      row.toggleSelected();
    }

    this.bhRowClick.emit({
      row: row.original,
      rowId,
      rowIndex: row.index,
    });
  };

  private handleCellClick = (row: Row<TData>, cell: Cell<TData, unknown>) => {
    this.bhCellClick.emit({
      row: row.original,
      rowId: row.id,
      rowIndex: row.index,
      columnId: cell.column.id,
      value: cell.getValue(),
    });
  };

  private handleHeaderClick = (header: Header<TData, unknown>) => {
    if (this.enableSorting && header.column.getCanSort()) {
      header.column.toggleSorting(undefined, this.enableMultiSort);
    }
  };

  private handleRowExpand = (row: Row<TData>) => {
    const wasExpanded = row.getIsExpanded();
    row.toggleExpanded();

    this.bhExpandChange.emit({
      rowId: row.id,
      expanded: !wasExpanded,
    });
  };

  private handleGroupToggle = (row: Row<TData>) => {
    const wasExpanded = row.getIsExpanded();
    row.toggleExpanded();

    this.bhGroupToggle.emit({
      groupId: row.id,
      expanded: !wasExpanded,
    });
  };

  private handleGlobalFilterChange = (event: Event) => {
    const input = event.target as HTMLInputElement;
    this.table?.setGlobalFilter(input.value);
  };

  private handleColumnFilterChange = (columnId: string, value: string) => {
    const column = this.table?.getColumn(columnId);
    if (column) {
      column.setFilterValue(value || undefined);
    }
  };

  private handlePaginationChange = (event: CustomEvent) => {
    const { page, pageSize } = event.detail;
    this.table?.setPagination({
      pageIndex: page - 1,
      pageSize,
    });
  };

  // ===========================================================================
  // Editing Handlers
  // ===========================================================================

  private startEditing = (row: Row<TData>, columnId?: string) => {
    if (!this.enableEditing) return;

    this.editingRowId = row.id;
    this.editingColumnId = this.editMode === 'cell' ? (columnId || null) : null;

    // Initialize editing values from current row
    const values: Record<string, unknown> = {};
    this.columns.forEach(col => {
      const colId = (col as { accessorKey?: string }).accessorKey || col.id;
      if (colId) {
        values[colId] = row.original[colId as keyof TData];
      }
    });
    this.editingValues = values;
    this.validationErrors = {};

    this.bhEditStart.emit({
      rowId: row.id,
      row: row.original,
      columnId,
    });
  };

  private cancelEditing = () => {
    if (!this.editingRowId) return;

    const row = this.table?.getRow(this.editingRowId);
    if (row) {
      this.bhEditCancel.emit({
        rowId: this.editingRowId,
        row: row.original,
      });
    }

    this.editingRowId = null;
    this.editingColumnId = null;
    this.editingValues = {};
    this.validationErrors = {};
  };

  private saveEditing = () => {
    if (!this.editingRowId || !this.table) return;

    const row = this.table.getRow(this.editingRowId);
    if (!row) return;

    // Validate all edited values
    const errors: Record<string, string> = {};
    for (const [columnId, value] of Object.entries(this.editingValues)) {
      const column = this.table.getColumn(columnId);
      const validate = column?.columnDef.meta?.validate;
      if (validate) {
        const error = validate(value, row.original);
        if (error) {
          errors[columnId] = error;
          this.bhValidationError.emit({
            rowId: this.editingRowId,
            columnId,
            value,
            error,
          });
        }
      }
    }

    if (Object.keys(errors).length > 0) {
      this.validationErrors = errors;
      return;
    }

    // Determine changes
    const changes: Record<string, unknown> = {};
    for (const [columnId, newValue] of Object.entries(this.editingValues)) {
      const originalValue = row.original[columnId as keyof TData];
      if (originalValue !== newValue) {
        changes[columnId] = newValue;
      }
    }

    // Build new row
    const newRow = { ...row.original, ...changes } as TData;

    this.bhEditSave.emit({
      rowId: this.editingRowId,
      rowIndex: row.index,
      originalRow: row.original,
      changes,
      newRow,
    });

    this.editingRowId = null;
    this.editingColumnId = null;
    this.editingValues = {};
    this.validationErrors = {};
  };

  private handleEditValueChange = (columnId: string, newValue: unknown) => {
    const oldValue = this.editingValues[columnId];
    this.editingValues = { ...this.editingValues, [columnId]: newValue };

    // Clear validation error for this column
    if (this.validationErrors[columnId]) {
      const { [columnId]: _, ...rest } = this.validationErrors;
      this.validationErrors = rest;
    }

    if (this.editingRowId) {
      this.bhCellChange.emit({
        rowId: this.editingRowId,
        columnId,
        oldValue,
        newValue,
      });
    }
  };

  private handleEditKeyDown = (event: KeyboardEvent, columnId: string) => {
    if (event.key === 'Enter') {
      event.preventDefault();
      this.saveEditing();
    } else if (event.key === 'Escape') {
      event.preventDefault();
      this.cancelEditing();
    } else if (event.key === 'Tab' && this.editMode === 'cell') {
      event.preventDefault();
      this.moveToNextEditableCell(columnId, event.shiftKey);
    }
  };

  private moveToNextEditableCell = (currentColumnId: string, reverse: boolean) => {
    const editableColumns = this.columns.filter(col => col.meta?.editable !== false);
    const currentIndex = editableColumns.findIndex(col => {
      const colId = (col as { accessorKey?: string }).accessorKey || col.id;
      return colId === currentColumnId;
    });

    if (currentIndex === -1) return;

    const nextIndex = reverse ? currentIndex - 1 : currentIndex + 1;

    if (nextIndex >= 0 && nextIndex < editableColumns.length) {
      const nextCol = editableColumns[nextIndex];
      const nextColId = (nextCol as { accessorKey?: string }).accessorKey || nextCol.id;
      this.editingColumnId = nextColId || null;
    } else {
      this.saveEditing();
    }
  };

  private isEditing(rowId: string, columnId?: string): boolean {
    if (this.editingRowId !== rowId) return false;
    if (this.editMode === 'cell') {
      return columnId ? this.editingColumnId === columnId : false;
    }
    return true;
  }

  private handleCellDoubleClick = (row: Row<TData>, cell: Cell<TData, unknown>) => {
    if (!this.enableEditing || cell.column.columnDef.meta?.editable === false) return;
    if (this.editMode === 'cell') {
      this.startEditing(row, cell.column.id);
    }
  };

  // ===========================================================================
  // Render Helpers
  // ===========================================================================

  private flexRender(renderer: unknown, context: unknown): unknown {
    if (typeof renderer === 'function') {
      return renderer(context);
    }
    return renderer;
  }

  private formatCellValue(value: unknown, meta?: { type?: string; numberFormat?: Intl.NumberFormatOptions; dateFormat?: Intl.DateTimeFormatOptions }): string {
    if (value === null || value === undefined) return '';

    switch (meta?.type) {
      case 'number':
        return typeof value === 'number'
          ? value.toLocaleString(undefined, meta.numberFormat)
          : String(value);
      case 'date':
        return value instanceof Date
          ? value.toLocaleDateString(undefined, meta.dateFormat)
          : new Date(value as string).toLocaleDateString(undefined, meta.dateFormat);
      case 'boolean':
        return value ? 'Yes' : 'No';
      default:
        return String(value);
    }
  }

  // ===========================================================================
  // Render Methods
  // ===========================================================================

  private renderCaption() {
    if (!this.caption) return null;

    const captionClasses = {
      'data-grid-caption': true,
      [`data-grid-caption-${this.captionPosition}`]: true,
    };

    return (
      <div class={captionClasses}>
        <slot name="caption">{this.caption}</slot>
      </div>
    );
  }

  private renderGlobalFilter() {
    if (!this.enableGlobalFilter) return null;

    return (
      <div class="data-grid-global-filter">
        <span class="data-grid-search-icon material-symbols-outlined">search</span>
        <input
          type="text"
          class="data-grid-global-filter-input"
          placeholder={this.globalFilterPlaceholder}
          value={this.globalFilter}
          onInput={this.handleGlobalFilterChange}
        />
      </div>
    );
  }

  private renderHeader() {
    if (!this.table) return null;

    const headerGroups = this.table.getHeaderGroups();

    return (
      <div class={{ 'data-grid-header': true, 'data-grid-header-sticky': this.stickyHeader }}>
        {headerGroups.map(headerGroup => (
          <div class="data-grid-header-row" role="row" key={headerGroup.id}>
            {/* Selection column */}
            {this.renderSelectionHeader()}

            {/* Expansion column */}
            {this.enableExpansion && !this.enableGrouping && (
              <div class="data-grid-header-cell data-grid-cell-expand" role="columnheader" />
            )}

            {/* Data columns */}
            {headerGroup.headers.map(header => this.renderHeaderCell(header))}

            {/* Edit actions column */}
            {this.enableEditing && this.showEditActions && this.editMode === 'row' && (
              <div class="data-grid-header-cell data-grid-cell-actions" role="columnheader" />
            )}
          </div>
        ))}

        {/* Filter row */}
        {this.showFilterRow && this.renderFilterRow()}
      </div>
    );
  }

  private renderSelectionHeader() {
    if (this.selectionMode === 'none' || this.selectionMode === 'single-click' || this.selectionMode === 'multiple-click') {
      return null;
    }

    if (this.selectionMode === 'single-radio') {
      return <div class="data-grid-header-cell data-grid-cell-select" role="columnheader" />;
    }

    // Multiple checkbox - show select all
    const isAllSelected = this.table?.getIsAllRowsSelected();
    const isSomeSelected = this.table?.getIsSomeRowsSelected();

    return (
      <div class="data-grid-header-cell data-grid-cell-select" role="columnheader">
        <bh-checkbox
          size="sm"
          checked={isAllSelected}
          indeterminate={isSomeSelected && !isAllSelected}
          onBhChange={() => this.table?.toggleAllRowsSelected()}
        />
      </div>
    );
  }

  private renderHeaderCell(header: Header<TData, unknown>) {
    if (header.isPlaceholder) {
      return <div class="data-grid-header-cell" role="columnheader" key={header.id} />;
    }

    const canSort = header.column.getCanSort();
    const sortDirection = header.column.getIsSorted();
    const meta = header.column.columnDef.meta;

    const cellClasses = {
      'data-grid-header-cell': true,
      'data-grid-header-cell-sortable': canSort,
      'data-grid-header-cell-sorted': !!sortDirection,
      [`data-grid-cell-align-${meta?.align || 'left'}`]: true,
    };

    const style = header.column.getSize()
      ? { width: `${header.column.getSize()}px` }
      : {};

    return (
      <div
        class={cellClasses}
        style={style}
        role="columnheader"
        aria-sort={sortDirection === 'asc' ? 'ascending' : sortDirection === 'desc' ? 'descending' : 'none'}
        onClick={() => canSort && this.handleHeaderClick(header)}
        key={header.id}
      >
        <span class="data-grid-header-text">
          {this.flexRender(header.column.columnDef.header, header.getContext())}
        </span>
        {canSort && sortDirection && (
          <span class="data-grid-sort-icon material-symbols-outlined">
            {sortDirection === 'asc' ? 'arrow_upward' : 'arrow_downward'}
          </span>
        )}
      </div>
    );
  }

  private renderFilterRow() {
    if (!this.table) return null;

    const headers = this.table.getHeaderGroups()[0]?.headers || [];

    return (
      <div class="data-grid-filter-row" role="row">
        {/* Selection column placeholder */}
        {(this.selectionMode === 'single-radio' || this.selectionMode === 'multiple-checkbox') && (
          <div class="data-grid-filter-cell data-grid-cell-select" role="cell" />
        )}

        {/* Expansion column placeholder */}
        {this.enableExpansion && !this.enableGrouping && (
          <div class="data-grid-filter-cell data-grid-cell-expand" role="cell" />
        )}

        {/* Column filters */}
        {headers.map(header => {
          const canFilter = header.column.getCanFilter();
          const meta = header.column.columnDef.meta;

          if (!canFilter || meta?.filterable === false) {
            return <div class="data-grid-filter-cell" role="cell" key={header.id} />;
          }

          const filterValue = (header.column.getFilterValue() as string) ?? '';

          return (
            <div class="data-grid-filter-cell" role="cell" key={header.id}>
              <input
                type={meta?.type === 'number' ? 'number' : meta?.type === 'date' ? 'date' : 'text'}
                class="data-grid-filter-input"
                placeholder={`Filter...`}
                value={filterValue}
                onInput={(e) => this.handleColumnFilterChange(header.column.id, (e.target as HTMLInputElement).value)}
              />
            </div>
          );
        })}

        {/* Edit actions column placeholder */}
        {this.enableEditing && this.showEditActions && this.editMode === 'row' && (
          <div class="data-grid-filter-cell data-grid-cell-actions" role="cell" />
        )}
      </div>
    );
  }

  private renderBody() {
    if (!this.table) return null;

    // Loading skeleton
    if (this.loading && this.loadingType === 'skeleton') {
      return this.renderSkeleton();
    }

    const rows = this.table.getRowModel().rows;

    // Empty state
    if (rows.length === 0) {
      return this.renderEmptyState();
    }

    return (
      <div class="data-grid-body" role="rowgroup">
        {rows.map(row => {
          if (row.getIsGrouped()) {
            return this.renderGroupRow(row);
          }
          return this.renderDataRow(row);
        })}
      </div>
    );
  }

  private renderDataRow(row: Row<TData>) {
    const isSelected = row.getIsSelected();
    const isExpanded = row.getIsExpanded();
    const isRowEditing = this.editingRowId === row.id;

    const rowClasses = {
      'data-grid-row': true,
      'data-grid-row-selected': isSelected,
      'data-grid-row-expanded': isExpanded,
      'data-grid-row-editing': isRowEditing,
      'data-grid-row-hover': this.hoveredRowId === row.id,
      'data-grid-row-striped': this.striped && row.index % 2 === 1,
      'data-grid-row-clickable': this.selectionMode === 'single-click' || this.selectionMode === 'multiple-click',
    };

    return [
      <div
        class={rowClasses}
        role="row"
        aria-selected={isSelected ? 'true' : 'false'}
        key={row.id}
        onClick={() => this.handleRowClick(row)}
        onMouseEnter={() => { this.hoveredRowId = row.id; }}
        onMouseLeave={() => { this.hoveredRowId = null; }}
      >
        {/* Selection cell */}
        {this.renderSelectionCell(row)}

        {/* Expansion cell */}
        {this.enableExpansion && !this.enableGrouping && (
          <div class="data-grid-cell data-grid-cell-expand" role="cell" onClick={(e) => { e.stopPropagation(); }}>
            {row.getCanExpand() && (
              <button
                class="data-grid-expand-btn"
                onClick={() => this.handleRowExpand(row)}
                aria-label={isExpanded ? 'Collapse row' : 'Expand row'}
              >
                <span class="material-symbols-outlined">
                  {isExpanded ? 'expand_more' : 'chevron_right'}
                </span>
              </button>
            )}
          </div>
        )}

        {/* Data cells */}
        {row.getVisibleCells().map(cell => this.renderCell(row, cell))}

        {/* Edit actions cell */}
        {this.enableEditing && this.showEditActions && this.editMode === 'row' && (
          <div class="data-grid-cell data-grid-cell-actions" role="cell" onClick={(e) => e.stopPropagation()}>
            {isRowEditing ? (
              <div class="data-grid-edit-actions">
                <button class="data-grid-action-btn" onClick={() => this.saveEditing()} title="Save">
                  <span class="material-symbols-outlined">check</span>
                </button>
                <button class="data-grid-action-btn" onClick={() => this.cancelEditing()} title="Cancel">
                  <span class="material-symbols-outlined">close</span>
                </button>
              </div>
            ) : (
              <button class="data-grid-action-btn" onClick={() => this.startEditing(row)} title="Edit">
                <span class="material-symbols-outlined">edit</span>
              </button>
            )}
          </div>
        )}
      </div>,

      // Expanded content
      isExpanded && this.enableExpansion && !this.enableGrouping && (
        <div class="data-grid-row-expansion" key={`${row.id}-expansion`}>
          <slot name="expanded-row" />
        </div>
      ),
    ];
  }

  private renderSelectionCell(row: Row<TData>) {
    if (this.selectionMode === 'none' || this.selectionMode === 'single-click' || this.selectionMode === 'multiple-click') {
      return null;
    }

    const isSelected = row.getIsSelected();

    if (this.selectionMode === 'single-radio') {
      return (
        <div class="data-grid-cell data-grid-cell-select" role="cell" onClick={(e) => e.stopPropagation()}>
          <bh-radio-button
            selected={isSelected}
            onBhChange={() => {
              this.table?.resetRowSelection();
              row.toggleSelected(true);
            }}
          />
        </div>
      );
    }

    // Multiple checkbox
    return (
      <div class="data-grid-cell data-grid-cell-select" role="cell" onClick={(e) => e.stopPropagation()}>
        <bh-checkbox
          size="sm"
          checked={isSelected}
          onBhChange={() => row.toggleSelected()}
        />
      </div>
    );
  }

  private renderCell(row: Row<TData>, cell: Cell<TData, unknown>) {
    const meta = cell.column.columnDef.meta;
    const columnId = cell.column.id;
    const isCellEditing = this.isEditing(row.id, columnId);
    const canEdit = this.enableEditing && meta?.editable !== false;
    const hasError = !!this.validationErrors[columnId];

    const cellClasses = {
      'data-grid-cell': true,
      [`data-grid-cell-align-${meta?.align || (meta?.type === 'number' ? 'right' : 'left')}`]: true,
      [`data-grid-cell-type-${meta?.type || 'text'}`]: true,
      'data-grid-cell-editing': isCellEditing,
      'data-grid-cell-editable': canEdit && !isCellEditing,
      'data-grid-cell-error': hasError,
    };

    const style = cell.column.getSize()
      ? { width: `${cell.column.getSize()}px` }
      : {};

    return (
      <div
        class={cellClasses}
        style={style}
        role="cell"
        key={cell.id}
        onClick={(e) => {
          e.stopPropagation();
          this.handleCellClick(row, cell);
        }}
        onDblClick={(e) => {
          e.stopPropagation();
          this.handleCellDoubleClick(row, cell);
        }}
      >
        {isCellEditing ? this.renderEditInput(cell) : this.renderCellContent(cell)}
        {hasError && (
          <span class="data-grid-cell-error-text">{this.validationErrors[columnId]}</span>
        )}
      </div>
    );
  }

  private renderCellContent(cell: Cell<TData, unknown>) {
    const rendered = this.flexRender(cell.column.columnDef.cell, cell.getContext());

    // If custom cell renderer returns something, use it
    if (rendered !== undefined && rendered !== null && rendered !== cell.getValue()) {
      return rendered;
    }

    // Default formatting
    return this.formatCellValue(cell.getValue(), cell.column.columnDef.meta);
  }

  private renderEditInput(cell: Cell<TData, unknown>) {
    const columnId = cell.column.id;
    const meta = cell.column.columnDef.meta;
    const currentValue = this.editingValues[columnId] ?? cell.getValue();

    const inputType = meta?.type === 'number' ? 'number'
      : meta?.type === 'date' ? 'date'
      : meta?.type === 'boolean' ? 'checkbox'
      : 'text';

    if (meta?.type === 'boolean') {
      return (
        <bh-checkbox
          size="sm"
          checked={Boolean(currentValue)}
          onBhChange={(e: CustomEvent) => this.handleEditValueChange(columnId, e.detail.checked)}
        />
      );
    }

    return (
      <input
        type={inputType}
        class="data-grid-edit-input"
        value={currentValue != null ? String(currentValue) : ''}
        onInput={(e) => {
          const input = e.target as HTMLInputElement;
          const newValue = meta?.type === 'number' ? Number(input.value) : input.value;
          this.handleEditValueChange(columnId, newValue);
        }}
        onKeyDown={(e) => this.handleEditKeyDown(e, columnId)}
        ref={(el) => el && requestAnimationFrame(() => el.focus())}
      />
    );
  }

  private renderGroupRow(row: Row<TData>): (HTMLElement | unknown)[] {
    const isExpanded = row.getIsExpanded();
    const groupingColumnId = row.groupingColumnId!;
    const groupValue = row.groupingValue;
    const leafRows = row.getLeafRows();

    // Check if all leaf rows are selected for group selection
    const allSelected = leafRows.every(r => r.getIsSelected());
    const someSelected = leafRows.some(r => r.getIsSelected());

    const rowClasses = {
      'data-grid-row': true,
      'data-grid-row-group': true,
      'data-grid-row-group-expanded': isExpanded,
    };

    return [
      <div
        class={rowClasses}
        role="row"
        aria-expanded={isExpanded ? 'true' : 'false'}
        key={row.id}
        onClick={() => this.handleGroupToggle(row)}
      >
        {/* Group selection checkbox */}
        {this.selectionMode === 'multiple-checkbox' && (
          <div class="data-grid-cell data-grid-cell-select" role="cell" onClick={(e) => e.stopPropagation()}>
            <bh-checkbox
              size="sm"
              checked={allSelected}
              indeterminate={someSelected && !allSelected}
              onBhChange={() => {
                leafRows.forEach(r => r.toggleSelected(!allSelected));
              }}
            />
          </div>
        )}

        {/* Group toggle and label */}
        <div class="data-grid-cell data-grid-cell-group-label" role="cell">
          <button class="data-grid-group-toggle" aria-label={isExpanded ? 'Collapse group' : 'Expand group'}>
            <span class="material-symbols-outlined">
              {isExpanded ? 'expand_more' : 'chevron_right'}
            </span>
          </button>
          <span class="data-grid-group-label">
            <span class="data-grid-group-column">{groupingColumnId}: </span>
            <span class="data-grid-group-value">{String(groupValue)}</span>
          </span>
          <span class="data-grid-group-count">
            ({leafRows.length} {leafRows.length === 1 ? 'item' : 'items'})
          </span>
        </div>
      </div>,

      // Render child rows if expanded
      isExpanded && row.subRows.map(subRow => {
        if (subRow.getIsGrouped()) {
          return this.renderGroupRow(subRow);
        }
        return this.renderDataRow(subRow);
      }),
    ];
  }

  private renderEmptyState() {
    return (
      <div class="data-grid-empty">
        <bh-empty-state
          emptyTitle={this.emptyTitle}
          description={this.emptyDescription}
        />
      </div>
    );
  }

  private renderSkeleton() {
    const skeletonRows = Array.from({ length: this.skeletonRowCount }, (_, i) => i);
    const columnCount = this.columns.length +
      (this.selectionMode === 'single-radio' || this.selectionMode === 'multiple-checkbox' ? 1 : 0) +
      (this.enableExpansion && !this.enableGrouping ? 1 : 0) +
      (this.enableEditing && this.showEditActions && this.editMode === 'row' ? 1 : 0);

    return (
      <div class="data-grid-body data-grid-skeleton" role="rowgroup">
        {skeletonRows.map(i => (
          <div class="data-grid-row data-grid-row-skeleton" role="row" key={i}>
            {Array.from({ length: columnCount }, (_, j) => (
              <div class="data-grid-cell" role="cell" key={j}>
                <bh-skeleton-loader width="80%" height="16px" />
              </div>
            ))}
          </div>
        ))}
      </div>
    );
  }

  private renderLoadingOverlay() {
    if (!this.loading || this.loadingType !== 'spinner') return null;

    return (
      <div class="data-grid-loading-overlay">
        <bh-loader-spinner size="lg" />
      </div>
    );
  }

  private renderPagination() {
    if (!this.enablePagination || !this.table) return null;

    const rowCount = this.table.getFilteredRowModel().rows.length;

    return (
      <bh-pagination
        page={this.page}
        totalItems={rowCount}
        pageSize={this.pageSize}
        pageSizeOptions={this.pageSizeOptions}
        onBhPageChange={this.handlePaginationChange}
        onBhPageSizeChange={this.handlePaginationChange}
      />
    );
  }

  // ===========================================================================
  // Main Render
  // ===========================================================================

  render() {
    const gridClasses = {
      'data-grid': true,
      [`data-grid-${this.size}`]: true,
      'data-grid-bordered': this.bordered,
      'data-grid-striped': this.striped,
      'data-grid-loading': this.loading,
    };

    const containerStyle = this.maxHeight
      ? { maxHeight: this.maxHeight, overflowY: 'auto' as const }
      : {};

    return (
      <div class={gridClasses} role="grid" aria-busy={this.loading ? 'true' : 'false'}>
        {/* Caption (top) */}
        {this.captionPosition === 'top' && this.renderCaption()}

        {/* Global filter */}
        {this.renderGlobalFilter()}

        {/* Table container */}
        <div class="data-grid-container" style={containerStyle}>
          <div class="data-grid-table">
            {this.renderHeader()}
            {this.renderBody()}
          </div>
          {this.renderLoadingOverlay()}
        </div>

        {/* Caption (bottom) */}
        {this.captionPosition === 'bottom' && this.renderCaption()}

        {/* Pagination */}
        {this.renderPagination()}
      </div>
    );
  }
}
